.bi-block-control-icon {
  font-size: 1.25rem;
  flex: 0 0 auto;
}

.components-dropdown-menu__popover.popover-align-text-start .components-dropdown-menu__menu > button {
  text-align: left;
  text-align: start;
}

hr.gutestrap-block-clear-placeholder {
  color: grey;
}
hr.gutestrap-block-clear-placeholder::after {
  content: "\f314";
  font-family: dashicons;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0 0.5em;
  background-color: white;
}

.wp-block[data-type="gutestrap/clear"] {
  clear: both !important;
}

.block-editor-block-list__block[data-title][data-type="gutestrap/custom-scss"] {
  font-size: initial;
  margin: auto;
  border: thin dashed lightgrey;
  padding: 0.5em;
  padding-left: 0;
  border-radius: 0.5em;
  background: whitesmoke;
  max-width: 120ch !important;
}
.block-editor-block-list__block[data-title][data-type="gutestrap/custom-scss"]::before {
  padding: 0 0 0.75em 1em;
}

.gutestrap-custom-scss-editor {
  font-size: initial;
}
.components-modal__frame .gutestrap-custom-scss-editor textarea,
.components-modal__frame .gutestrap-custom-scss-editor .CodeMirror {
  width: 80vw;
  height: 50vh;
  min-height: 10em;
  max-width: 120ch;
}

.gutestrap-responsive-tabs .components-tab-panel__tabs-item {
  padding: 0;
  width: calc(99% / 8);
  justify-content: center;
}
.gutestrap-responsive-tabs .components-tab-panel__tabs-item:focus, .gutestrap-responsive-tabs .components-tab-panel__tabs-item.is-active:focus {
  box-shadow: inset 0 0 0 1.5px transparent, inset 0 -4px 0 0 #007cba;
  box-shadow: inset 0 0 0 1.5px transparent, inset 0 -4px 0 0 var(--wp-admin-theme-color);
  outline: none;
}
.gutestrap-responsive-tabs .components-tab-panel__tabs-item.has-icon-title .bi {
  font-size: 1.333em;
}
.gutestrap-responsive-tabs .components-tab-panel__tabs-item.has-notification > span {
  position: relative;
}
.gutestrap-responsive-tabs .components-tab-panel__tabs-item.has-notification > span::after {
  content: "";
  width: 8px;
  height: 8px;
  top: -2px;
  right: -2px;
  border-radius: 50%;
  display: block;
  position: absolute;
  background-color: dodgerblue;
  box-shadow: inset 1px 1px 3px -1px aliceblue;
}
.gutestrap-responsive-tabs .components-tab-panel__tabs-item.has-notification-info {
  background-color: dodgerblue;
  box-shadow: inset 1px 1px 3px -1px aliceblue;
}
.gutestrap-responsive-tabs .components-tab-panel__tabs-item.has-notification-warn span::after {
  background-color: gold;
  box-shadow: inset 2px 2px 2px -1px lemonchiffon;
}
.gutestrap-responsive-tabs .components-tab-panel__tabs-item.has-notification-error span::after {
  background-color: orangered;
  box-shadow: inset 2px 2px 2px -1px tomato;
}